/*------------------------------------*    $Toggle content
\*------------------------------------*/
.toggle--trigger {
  display: block;
  padding: 15px;
  margin-bottom: 15px;
  color: #6e6e6e;
  border-top: 2px dotted #6e6e6e;
  border-bottom: 2px dotted #6e6e6e;
  background: #ffffff;
  transition: background-color 300ms ease-out;
}

.toggle--trigger:hover, .toggle--trigger:focus, .toggle--trigger:active {
  color: #00a1cc;
  background: #f3f3f3;
  border-top: 2px dotted #6e6e6e;
  border-bottom: 2px dotted #6e6e6e;
}

.toggle--trigger:hover .toggle--trigger_icon:before {
  color: #00a1cc;
}

.toggle--trigger_icon {
  float: right;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  transition: all 300ms ease-out;
}

.toggle--trigger_icon:hover {
  color: #00a1cc;
}

.toggle--trigger_icon:before {
  font-size: 1em;
  color: #6e6e6e;
}

.toggle--trigger.is--active {
  color: #00a1cc;
}

.toggle--trigger.is--active span {
  -ms-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.toggle--trigger.is--active span:before {
  color: #00a1cc;
}

.toggle--content {
  padding: 0 15px;
  margin-bottom: 0;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
  -webkit-transition: 1s;
  transition: 1s;
  overflow: hidden;
}

.toggle--content.is--expanded {
  max-height: 999px !important;
  opacity: 1 !important;
}

.no-js .toggle--trigger_icon {
  display: none;
}

/*------------------------------------*   $Accordion
\*------------------------------------*/
.accordion {
  overflow: hidden;
  border-radius: 3px;
  background: transparent;
  border-top: 2px dotted #6e6e6e;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.accordion .accordion-section .accordion-section-title {
  width: 100%;
