  background: #f3f3f3;
  text-decoration: none;
  border-bottom: 2px dotted #6e6e6e;
  color: #00a1cc;
}

.accordion .accordion-section .accordion-section-title:focus {
  color: #00a1cc;
  background: #f3f3f3;
}

.accordion .accordion-section .accordion-section-title:focus span {
  transition: all 300ms ease-out;
}

.accordion .accordion-section .accordion-section-title:focus span:before {
  color: #00a1cc;
}

.accordion .accordion-section .accordion-section-title .accordion--trigger_icon {
  float: right;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  transition: all 300ms ease-out;
}

.accordion .accordion-section .accordion-section-title .accordion--trigger_icon:hover {
  color: #00a1cc;
}

.accordion .accordion-section .accordion-section-title .accordion--trigger_icon:before {
  font-size: 1em;
  color: #6e6e6e;
}

.accordion .accordion-section .accordion-section-title:hover {
  color: #00a1cc;
}

.accordion .accordion-section .accordion-section-title:hover .accordion--trigger_icon:before {
  color: #00a1cc;
}

.accordion .accordion-section .is--active {
  color: #00a1cc;
}

.accordion .accordion-section .is--active .accordion--trigger_icon:before {
  color: #00a1cc;
}

.accordion .accordion-section .accordion-section-content {
  display: none;
}

.accordion .accordion-section .accordion-section-content p {
  margin-bottom: 0;
  padding: 15px 0;
}

.toggle--trigger.is--active {
  color: #00a1cc;
}

.toggle--trigger.is--active span:before {
  color: #00a1cc;
}

.accordion-section-content {
  overflow: hidden;
  display: block !important;
  padding: 0 15px;
  margin-bottom: 0;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
  -webkit-transition: 1s;
  transition: 1s;
}

.accordion-section-content.open {
  max-height: 999px !important;
  opacity: 1 !important;
}

.no-js .accordion--trigger_icon {
  display: none;
}

